import { Component, Input, OnInit } from '@angular/core';
import {ComedyVidsService} from "../comedy-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-comedy-skel',
  templateUrl: './comedy-skel.component.html',
  styleUrls: ['./comedy-skel.component.css']
})
export class ComedySkelComponent implements OnInit {
  @Input() comVidsData: any={}
  comVidsObj: any={}
    constructor(private dataservice: ComedyVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.comVidsObj= this.dataservice.getComVidById(this.activatedRoute.snapshot.params.id);
    }
  
}
