import { Component, Input, OnInit } from '@angular/core';
import {RomanceVidsService} from "../romance-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-romance-skel',
  templateUrl: './romance-skel.component.html',
  styleUrls: ['./romance-skel.component.css']
})
export class RomanceSkelComponent implements OnInit {
  @Input() romVidsData: any={}
  romVidsObj: any={}
    constructor(private dataservice: RomanceVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.romVidsObj= this.dataservice.getRomVidById(this.activatedRoute.snapshot.params.id);
    }

}
