import { Component, OnInit } from '@angular/core';
import { from } from 'rxjs';
import {NewsVidsService} from "../news-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-news-area',
  templateUrl: './news-area.component.html',
  styleUrls: ['./news-area.component.css']
})
export class NewsAreaComponent implements OnInit {
  newsVidsListing:Array<Object>=[]
  newsVidsObj: any={};
    constructor(private dataservice: NewsVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.newsVidsListing= this.dataservice.getNewsVids();
      this.newsVidsObj= this.dataservice.getNewsVidById(this.activatedRoute.snapshot.params.id);
    }

}
