import { Component, OnInit } from '@angular/core';
import {TotalVidsService} from "../total-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-video-area',
  templateUrl: './video-area.component.html',
  styleUrls: ['./video-area.component.css']
})
export class VideoAreaComponent implements OnInit {
totalVidsListing:Array<Object>=[]
totalVidsObj: any={};
  constructor(private dataservice: TotalVidsService, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    console.log(this.activatedRoute.snapshot.params.id);
    this.totalVidsListing= this.dataservice.getAllVids();
    this.totalVidsObj= this.dataservice.getVidById(this.activatedRoute.snapshot.params.id);
  }

}
