import { Component, Input, OnInit } from '@angular/core';
import {SportsVidsService} from "../sports-vids.service";
import {ActivatedRoute} from "@angular/router";
import { SportsComponent } from '../sports/sports.component';
@Component({
  selector: 'app-sports-skel',
  templateUrl: './sports-skel.component.html',
  styleUrls: ['./sports-skel.component.css']
})
export class SportsSkelComponent implements OnInit {

  @Input() sportsVidsData: any={}
  sportsVidsObj: any={}
    constructor(private dataservice: SportsVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.sportsVidsObj= this.dataservice.getSportsVidById(this.activatedRoute.snapshot.params.id);
    }
}
