import { Component, OnInit } from '@angular/core';
import {SportsVidsService} from "../sports-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-sports-area',
  templateUrl: './sports-area.component.html',
  styleUrls: ['./sports-area.component.css']
})
export class SportsAreaComponent implements OnInit {

  sportsVidsListing:Array<Object>=[]
sportsVidsObj: any={};
  constructor(private dataservice: SportsVidsService, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    console.log(this.activatedRoute.snapshot.params.id);
    this.sportsVidsListing= this.dataservice.getSportsVids();
    this.sportsVidsObj= this.dataservice.getSportsVidById(this.activatedRoute.snapshot.params.id);
  }

}
