import { Component, Input, OnInit } from '@angular/core';
import {NewsVidsService} from "../news-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-news-skel',
  templateUrl: './news-skel.component.html',
  styleUrls: ['./news-skel.component.css']
})
export class NewsSkelComponent implements OnInit {
  @Input() newsVidsData: any={}
  newsVidsObj: any={}
    constructor(private dataservice: NewsVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.newsVidsObj= this.dataservice.getNewsVidById(this.activatedRoute.snapshot.params.id);
    }
}
