import { Component, Input, OnInit } from '@angular/core';
import {TotalVidsService} from "../total-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-videoskel',
  templateUrl: './videoskel.component.html',
  styleUrls: ['./videoskel.component.css']
})
export class VideoskelComponent implements OnInit {
@Input() totalVidsData: any={}
totalVidsObj: any={}
  constructor(private dataservice: TotalVidsService, private activatedRoute: ActivatedRoute) { }

  ngOnInit(): void {
    console.log(this.activatedRoute.snapshot.params.id);
    this.totalVidsObj= this.dataservice.getVidById(this.activatedRoute.snapshot.params.id);
  }

}
