import { Component, OnInit } from '@angular/core';
import {ComedyVidsService} from "../comedy-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-comedy-area',
  templateUrl: './comedy-area.component.html',
  styleUrls: ['./comedy-area.component.css']
})
export class ComedyAreaComponent implements OnInit {
  comVidsListing:Array<Object>=[]
  comVidsObj: any={};
    constructor(private dataservice: ComedyVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.comVidsListing= this.dataservice.getComVids();
      this.comVidsObj= this.dataservice.getComVidById(this.activatedRoute.snapshot.params.id);
    }

}
