import { Component, OnInit } from '@angular/core';
import {RomanceVidsService} from "../romance-vids.service";
import {ActivatedRoute} from "@angular/router";
@Component({
  selector: 'app-romance-area',
  templateUrl: './romance-area.component.html',
  styleUrls: ['./romance-area.component.css']
})
export class RomanceAreaComponent implements OnInit {
  romVidsListing:Array<Object>=[]
  romVidsObj: any={};
    constructor(private dataservice: RomanceVidsService, private activatedRoute: ActivatedRoute) { }
  
    ngOnInit(): void {
      console.log(this.activatedRoute.snapshot.params.id);
      this.romVidsListing= this.dataservice.getRomVids();
      this.romVidsObj= this.dataservice.getRomVidById(this.activatedRoute.snapshot.params.id);
    }

}
