import { Component, OnInit } from '@angular/core';
import { AuthService } from './auth.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit{
  title = 'angularRecipe';
  isAuthenticated: boolean = false;
  username: any;

  constructor(public authService: AuthService) {
    this.authService.isAuthenticated.subscribe((isAuthenticated: boolean) => this.isAuthenticated = isAuthenticated);
    this.authService.user.subscribe((username: any) => this.username = username);
  }

  ngOnInit() {
    this.isAuthenticated = this.authService.checkAuthenticated();
    this.username = this.authService.checkUser();
  }

  logout() {
    this.authService.logout('/');
  }


}
