import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, from } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  readonly APIUrl = 'http://localhost:5000/api';
  dataVal: any;
  public isAuthenticated = new BehaviorSubject<boolean>(false);
  public user = new BehaviorSubject<any>(null);
  public users: any[] = [
    new Map([
      ['username', 'bharat'],
      ['password', 'bharat123'],
    ]),
  ];
  public usersUpdated = new BehaviorSubject<Map<string, any>[]>(this.users);

  constructor(private http: HttpClient, private router: Router) {}

  async login(username: string, password: string): Promise<boolean> {
    this.dataVal = await this.getUser(username);
    if (this.dataVal.length === 1) {
      var user = this.dataVal[0];
      if (password === user['pwd']) {
        this.isAuthenticated.next(true);
        this.user.next(username);
        this.router.navigate(['/']);
        return false;
      } else return true;
    } else return true;
  }

  checkAuthenticated() {
    const authenticated = this.user.value === null ? false : true;
    this.isAuthenticated.next(authenticated);
    return authenticated;
  }

  checkUser() {
    const username = this.user;
    return username;
  }

  logout(redirect: string) {
    this.isAuthenticated.next(false);
    this.user.next(null);
    this.router.navigate([redirect]);
  }

  signup(username: string, password: string, email: string) {
    this.addUser({ userName: username, pwd: password, email: email }).subscribe(
      (res) => {
        alert(res.toString());
        if (res.toString().includes('already taken')) return;
        else this.router.navigate(['login']);
      }
    );
    // let userMap = new Map([
    //   ['username', username],
    //   ['password', password],
    // ]);
    // this.users.push(userMap);
    // this.usersUpdated.next([...this.users]);
  }

  getUserList(): Observable<any[]> {
    return this.http.get<any>(this.APIUrl + '/user');
  }

  getUser(username: string) {
    return this.http.get(this.APIUrl + '/user/' + username).toPromise();
  }

  addUser(val: any) {
    return this.http.post(this.APIUrl + '/user', val);
  }

  updateUser(val: any) {
    return this.http.put(this.APIUrl + '/user', val);
  }

  deleteUser(val: any) {
    return this.http.delete(this.APIUrl + '/user/' + val);
  }
}
